import React from "react";

function LondonVreme() {
  const lnd = new Date().toLocaleString("en-GB", {
    timeZone: "Europe/London",
    hour12: true
  });
  return lnd;
}

function ParisVreme() {
  const prs = new Date().toLocaleString("en-GB", { timeZone: "Europe/Paris" });
  return prs;
}

function NYVreme() {
  const ny = new Date().toLocaleString("en-US", {
    timeZone: "America/New_York"
  });
  return ny;
}
function SPVreme() {
  const ny = new Date().toLocaleString("en-US", {
    timeZone: "America/Sao_Paulo"
  });
  return ny;
}
function MoscowVreme() {
  const mos = new Date().toLocaleString("en-GB", {
    timeZone: "Europe/Moscow",
    hour12: false
  });
  return mos;
}
function BgdVreme() {
  const bgd = new Date().toLocaleString("en-GB", {
    timeZone: "Europe/Belgrade",
    hour12: false
  });
  return bgd;
}

function HKVreme() {
  const pek = new Date().toLocaleString("en-GB", {
    timeZone: "Asia/Hong_Kong",
    hour12: false
  });
  return pek;
}

function SydneyVreme() {
  const syd = new Date().toLocaleString("en-GB", {
    timeZone: "Australia/Sydney",
    hour12: true
  });
  return syd;
}

export {
  LondonVreme,
  ParisVreme,
  NYVreme,
  SPVreme,
  MoscowVreme,
  BgdVreme,
  HKVreme,
  SydneyVreme
};
