import React from "react";
import createRoot from "react-dom";
import "./styles.css";
import {
  LondonVreme,
  NYVreme,
  ParisVreme,
  SPVreme,
  MoscowVreme,
  BgdVreme,
  HKVreme,
  SydneyVreme
} from "./vremena";

function App() {
  function prikazilnd() {
    createRoot.render(<LondonVreme />, document.getElementById("vreme"));
  }
  function prikazipar() {
    createRoot.render(<ParisVreme />, document.getElementById("vreme"));
  }
  function prikaziny() {
    createRoot.render(<NYVreme />, document.getElementById("vreme"));
  }
  function prikazisp() {
    createRoot.render(<SPVreme />, document.getElementById("vreme"));
  }
  function prikazimos() {
    createRoot.render(<MoscowVreme />, document.getElementById("vreme"));
  }
  function prikazibgd() {
    createRoot.render(<BgdVreme />, document.getElementById("vreme"));
  }
  function prikazipek() {
    createRoot.render(<HKVreme />, document.getElementById("vreme"));
  }
  function prikazisyd() {
    createRoot.render(<SydneyVreme />, document.getElementById("vreme"));
  }
  return (
    <div className="App">
      <h1>Current time</h1>
      <div id="vreme"></div>
      <div id="gradovi">
        <button onClick={prikazilnd} id="dugme" className="jedan">
          London
        </button>
        <button onClick={prikazipar} id="dugme" className="dva">
          Paris
        </button>
        <button onClick={prikaziny} id="dugme" className="tri">
          New York
        </button>
        <button onClick={prikazisp} id="dugme" className="cetri">
          Sao Paulo
        </button>
        <button onClick={prikazimos} id="dugme" className="pet">
          Moscow
        </button>
        <button onClick={prikazibgd} id="dugme" className="sest">
          Belgrade
        </button>
        <button onClick={prikazipek} id="dugme" className="sest">
          Hong Kong
        </button>
        <button onClick={prikazisyd} id="dugme" className="sest">
          Sydney
        </button>
      </div>
      <h2>Click the city to get exact time!</h2>
    </div>
  );
}

export default App;
